<template>
  <waterfall :line-gap="winWidth" :watch="items">
    <waterfall-slot
      v-for="(item, index) in items"
      :width="item.width"
      :height="item.height"
      :order="item.index"
      :key="index"
    >
      <div class="p-2">
        <div class="border rounded p-1">
          <div>{{item.name}}</div>
          <div v-html="item.content"></div>
          <div>
            <a
              href="https://raw.githubusercontent.com/Ace520/assets/master/sheet/"
              target="_brack"
            >GitHub</a>
          </div>
        </div>
      </div>
    </waterfall-slot>
  </waterfall>
</template>

<script>
import Waterfall from "vue-waterfall/lib/waterfall";
import WaterfallSlot from "vue-waterfall/lib/waterfall-slot";
import axios from "axios";
export default {
  name: "App",
  components: {
    Waterfall,
    WaterfallSlot
  },
  data() {
    return {
      config: {},
      items: [],
      winWidth: 0,
      md: {}
    };
  },
  mounted() {
    let MarkdownIt = require("markdown-it");
    this.md = new MarkdownIt();
    this.winWidth = this.getWidth();
    let config = require("../../md/" + this.$route.params.id + "/index.json");
    this.initItems(config.items);
  },
  methods: {
    initItems(val) {
      for (let i = 0; i < val.length; i++) {
        axios
          .get("../../md/" + this.$route.params.id + "/" + val[i].path)
          .then(res => {
            let content = this.md.render(
              "<div class='markdown-body'>" + res.data + "</div>"
            );
            this.items.push({
              title: val[i].title,
              width: this.winWidth,
              height: this.getHeight(content),
              index: i,
              content: content
            });
          })
          .catch(error => {
            console.error(error);
          });
      }
    },
    getHeight(html) {
      let div = document.createElement("div");
      div.innerText = html;
      document.body.appendChild(div);
      let h = div.scrollHeight;
      div.remove();
      return h;
    },
    getWidth() {
      let w = document.body.clientWidth;
      if (w < 350) {
        return w;
      }
      return w / parseInt(w / 350);
    }
  }
};
</script>

